@import url('https://fonts.googleapis.com/css2?family=PT+Sans:wght@400&display=swap');

.Directory {
  background-image: url('../../img/Triangle-White-Seamless-Patterns.jpg');
  /* background-image: url("../../img/Light-paper-fibers-Seamless-Pattern.jpg"); */
  /* background-image: url("../../img/White-Brick-Wallpaper1.jpg"); */
}

.Directory nav {
  background-color: rgba(238, 238, 238, 0.6);
  font-family: 'PT Sans', sans-serif;
  font-size: 20px;
}

.Directory ul {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  text-align: center;
}

.Directory li {
  display: inline-block;
  margin: 20px;
}

.Directory a {
  text-decoration: none;
  color: black;
  margin: 0 20px;
  font-weight: bold;
}

.Directory a:hover,
.Directory a:active {
  color: #13cbe7;
}
