@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.Directory {
  background-image: url(/static/media/Triangle-White-Seamless-Patterns.9e8a87f2.jpg);
  /* background-image: url("../../img/Light-paper-fibers-Seamless-Pattern.jpg"); */
  /* background-image: url("../../img/White-Brick-Wallpaper1.jpg"); */
}

.Directory nav {
  background-color: rgba(238, 238, 238, 0.6);
  font-family: 'PT Sans', sans-serif;
  font-size: 20px;
}

.Directory ul {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  text-align: center;
}

.Directory li {
  display: inline-block;
  margin: 20px;
}

.Directory a {
  text-decoration: none;
  color: black;
  margin: 0 20px;
  font-weight: bold;
}

.Directory a:hover,
.Directory a:active {
  color: #13cbe7;
}

.Person {
  font-family: 'Montserrat', sans-serif;
  min-width: 22rem;
  padding: 16px;
  text-align: center;
  border: 1px solid #eee;
  border-radius: 8px;
  /* box-shadow: 0 2px 3px #ccc; */
  box-shadow: rgb(176 176 176) 0px 0px 0px 1px inset;
  margin: 10px;
  box-sizing: border-box;
  transition: all 1 ease-in;
  background-color: white;
}

.Person:hover,
.Person:active {
  box-shadow: black 0px 0px 0px 2px inset;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: #eee;
}

.linkedin {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-decoration: none;
  cursor: pointer;
}

.email {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.emailImage {
  height: 25px;
  margin: 10px;
}

.Persons-wrapper {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.Persons-header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 80%;
  margin: auto;
}

.Persons {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  width: 70%;
  min-height: 80vh;
  margin: auto;
}

.logo {
  border: 3px solid black;
}

.home-wrapper {
  font-family: 'PT sans', sans-serif;
  height: 90vh;
}

.banner {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.banner-name {
  margin: 0;
  color: white;
  font-size: 1.8rem;
  position: absolute;
  top: 12%;
  left: 51%;
}

.banner-name h1 {
  margin: 0;
}

.home-flex-container {
  display: -webkit-flex;
  display: flex;
  /* align-items: center; */
  -webkit-justify-content: center;
          justify-content: center;
  width: 90%;
  margin: 15px auto;
}

.info {
  width: 70%;
  margin: 2rem;
  font-size: 1.5rem;
}

.next-head {
  margin-left: 25%;
}

.quote {
  font-style: italic;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.NewPerson {
  width: 70%;
  margin: 20px auto;
  border: 1px solid #eee;
  box-shadow: 0 2px 3px #ccc;
  text-align: center;
  font-family: 'PT Sans', sans-serif;
  font-size: 1rem;
  background-color: white;
}

.NewPerson label {
  display: block;
  width: 60%;
  margin: 15px auto;
  text-align: left;
  font-weight: bold;
}

.NewPerson input,
.NewPerson textarea,
.NewPerson select {
  display: block;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  background-color: #f2f2f2;
  border: 1px solid #d0d0d0;
  outline: none;
  font: inherit;
  margin: 15px 0px;
}

.NewPerson button {
  margin: 20px 0;
  margin-top: 30px;
  padding: 10px;
  font: inherit;
  border: 1px solid #13cbe7;
  background-color: transparent;
  color: #13cbe7;
  cursor: pointer;
}

.NewPerson button:hover,
.NewPerson button:active {
  color: white;
  background-color: #13cbe7;
}

.Company {
  font-family: 'Montserrat', sans-serif;
  min-width: 22rem;
  padding: 16px;
  text-align: center;
  border: 1px solid #eee;
  border-radius: 8px;
  /* box-shadow: 0 2px 3px #ccc; */
