}

.link {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.companyLogo {
  margin: 10px;
}

.Companies-body {
  min-height: 90vh;
}

.Companies-header {
  text-align: center;
}

.Companies {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  width: 60%;
  margin: auto;
}

